import React, { useEffect, useState } from "react";

declare global {
  interface Window {
    google: any;
  }
}

export const google = window.google;

const CLIENT_ID = "436749985502-sf198t6s3up8f8em1ls5occgd2uu5219.apps.googleusercontent.com";
const API_KEY = "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";
const SCOPES = "https://www.googleapis.com/auth/drive.readonly https://www.googleapis.com/auth/userinfo.profile";


export const GoogleAuthButton: React.FC<{ onTokenReceived: (token: string) => void }> = ({ onTokenReceived }) => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  const handleLogin = () => {
    const tokenClient = google.accounts.oauth2.initTokenClient({
      client_id: CLIENT_ID,
      scope: "https://www.googleapis.com/auth/drive.readonly",
      callback: (tokenResponse: any) => {
        const accessToken = tokenResponse.access_token;
        onTokenReceived(accessToken); // Pass token to parent or store
        setIsAuthenticated(true);
      },
    });

    tokenClient.requestAccessToken();
  };

  return (
    <button
      onClick={handleLogin}
      className="p-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600"
    >
      {isAuthenticated ? "Logged In" : "Login with Google"}
    </button>
  );
};